import React from 'react'
import ParticipantItem from './ParticipantItem'
import azer from '../images/vokalistler/azer.jpg'
import gulnaz from '../images/vokalistler/gulnaz.jpg'
import samir from '../images/vokalistler/samir.jpg'
import ContactLine from './ContactLine'
import { Link } from 'react-router-dom'

function ParticipantComponent() {
  return (
    <div className='w-screen  bg-slate-600 p-10 bg-transparent '>
            <ContactLine title='tiraklar' />
            <br />
            <br />
    <ParticipantItem  src={azer} name='Azr Zeynalov' title='xalq artisti' about='Azr Zeynalov 1964-c il dekabrn 13-d Naxvan hrind ziyal ailsind anadan olmudur. 1969-cu ildn Bakda yaayan Azr 1972-ci ild 132 sayl orta mktb v 12 sayl musiqi mktbinin fortepiano sinfin getmidir. 1982-ci ild Azrbaycan Dvlt Mdniyyt v ncsnt Universitetinin dram rejissorluu bsin daxil olmu, arada 2 il hrbi xidmt getmi v 1989-cu ild dram rejissoru ixtisas il thsilini baa vurmudur. 1990-c ild musiqiy olan sevgisi onu Rid Behbudov adna Dvlt Mahn Teatrna gtirmi v bir mnni kimi o, ilk addmlarn bu teatrn shnsind atmdr. ' />

    <ParticipantItem  src={gulnaz} name='Glnaz smaylova' title='xalq artisti' about='2001-ci ildn Azrbaycan Dvlt Akademik Opera v Balet Teatrn solistidir.1985-ci ildn Azrbaycan Dvlt Konservatoriyasnda mllim vzifsind alr. 2000-ci ildn ba mllimdir. 2007-ci ildn dosentidir. 2012-ci ildn Bak Musiqi Akademiyasnn professorudur.Hal-hazrda BMA-da Solo oxuma v fortepiano-dan drs deyir. 2006-c ild Azrbaycan Respublikasnn mkdar Artisti, 27 may 2018-ci ild Azrbaycann xalq artisti fxri adna layiq grlmdr.'/>

    <ParticipantItem  src={samir} name='Samir Cfrov' title='xalq artisti' about='19 noyabr 1976-c ild Naxvan MSSR, Babk rayonunun Nehrm kndind anadan olmudur. 19931997-ci illr rzind Azrbaycan Dvlt Mdniyyt v ncsnt Universitetind teatr v kino snti ixtisas zr, 19992001-ci illrd Bak Musiqi Akademiyasnda solo oxuma ixtisas zr thsil almdr. Azrbaycan Dvlt Akademik Opera v Balet Teatrn solistidir. 2001-ci ild Almatnda keiriln Vokallarn Beynalxalq Msabiqsinin laureat olmudur. 2002-ci ild Moskva hrind "Qalina Vinevskayann Opera Mrkzi"nd thsil almdr. 2017-ci ild Gnc Dvlt Filarmoniyasnn direktoru tyin edilib.'/>
    <Link to='/participants'>
       <p className='w-40  glass h-10 flex justify-center pt-1 scaling' > <span>Digr tiraklar</span> </p>
 </Link>
    </div>
  )
}

export default ParticipantComponent